import React, { BaseSyntheticEvent } from 'react';
import { inputWithLabelPropTypes } from './InputWithLabel.props';
import styles from './InputWithLabel.module.scss';
import ReactTooltip from 'react-tooltip';

type InputWithLabelProps = {
  label: any;
  onChange: (event: BaseSyntheticEvent) => void;
  value?: string;
  className?: string;
  disabled?: boolean;
  type?: string;
  onKeyUp?: (event: BaseSyntheticEvent) => void;
  onCopy?: boolean;

  isToolTip?: boolean;
  toolTipText?: string;
  toolTipID?: string;
  withIcon?: boolean;
  maxLength?: any;
  placeholder?: string;
};

/**
 *     
 *
 * @param {string} label -    
 * @param {func} onChange -    
 * @param {string} value -   
 * @param {string} [className] - CSS  
 * @param {boolean} [disabled] -       
 * @param {boolean} onCopy -  
 * @param {boolean} isToolTip -   
 * @param {string} toolTipText -   
 * @param {string} toolTipID - id  
 * @param {any} maxLength -     input
 */
const InputWithLabel = (props: InputWithLabelProps) => {
  const {
    label,
    onChange,
    value,
    disabled,
    type,
    onKeyUp,
    onCopy,
    isToolTip,
    toolTipText,
    toolTipID,
    withIcon,
    maxLength,
    placeholder,
    ...rest
  } = props;

  return (
    <div
      data-tip={isToolTip ? 'React-tooltip' : ''}
      data-for={isToolTip ? toolTipID : null}
      {...rest}
    >
      <span className={styles.label}>{label}</span>
      <input
        disabled={disabled}
        className={`${styles.custom_input} ${
          onCopy ? styles.onCopy : disabled ? styles.disabled : ''
        } ${withIcon ? styles.withIcon : ''}
        `}
        type={type ? type : 'text'}
        value={value}
        onChange={onChange}
        onKeyUp={onKeyUp}
        maxLength={maxLength}
        placeholder={placeholder}
      />
      {isToolTip ? (
        <ReactTooltip place="bottom" type="dark" effect="float" id={toolTipID} delayShow={200}>
          <span className={styles.spanTooltip}>{toolTipText}</span>
        </ReactTooltip>
      ) : (
        ''
      )}
    </div>
  );
};

InputWithLabel.propTypes = inputWithLabelPropTypes;

InputWithLabel.defaultProps = {
  disabled: false,
};

export default InputWithLabel;
