import React, { PureComponent } from 'react';
import styles from './ITAISParticipant.module.scss';

import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { inject, observer } from 'mobx-react';
import InputWithLabel from '../../../../InputWithLabel';

import { QUARTERS_LABELS } from '../../../../constants';

import { formatDate } from '../../../../../utils/ui/formatDate';
import { formatMoneyDetailedNode } from '../../../../../utils/ui/formatMoneyDetailedNode';

type ITAISParticipantProps = {
  graphStore?: GraphStoreType;
  nodeData: any;
  nodeDataMain: any;
  disabled: boolean;
  theme: any;
};

type ITAISParticipantState = {
  nodeData: any;
};

/**
 *       
 */
@inject('graphStore')
@observer
class ITAISParticipant extends PureComponent<ITAISParticipantProps, ITAISParticipantState> {
  constructor(props: ITAISParticipantProps) {
    super(props);

    const { nodeData } = props;

    this.state = {
      nodeData,
    };
  }

  /**
   *     
   */
  render() {
    const {
      props: { nodeData, disabled, nodeDataMain },
    } = this;

    const getQuarter = (d: any) => {
      return Math.floor((Number(d.slice(5, 7)) + 2) / 3);
    };

    const type_participant =
      nodeData?.participant_type === 'RU' ? 'ru_participant' : 'by_participant';

    let defaultValue;
    if (nodeData[type_participant]?.declaration) {
      const currentDate = nodeData[type_participant]?.declaration?.period;
      const currentYear = Number(currentDate.slice(0, 4));
      const currentQuarter = getQuarter(currentDate);
      const defaultQuarterIndex = currentQuarter - 1;
      defaultValue = `${QUARTERS_LABELS[defaultQuarterIndex]} ${currentYear}`;
    } else {
      defaultValue = null;
    }

    return (
      <div className={styles.wrapper}>
        <div className={styles.container}>
          <InputWithLabel
            disabled={disabled}
            className={styles.containerLarge}
            value={nodeDataMain.short_name ?? ' '}
            onChange={() => {}}
            label="  "
            onCopy={true}
          />

          <InputWithLabel
            disabled={disabled}
            className={styles.containerMinor}
            value={nodeDataMain.inn ?? ' '}
            onChange={() => {}}
            label=" "
            onCopy={true}
          />
        </div>
        <div className={styles.container}>
          {type_participant === 'ru_participant' && (
            <>
              <InputWithLabel
                disabled={disabled}
                className={styles.containerLarge}
                value={nodeData.director_name ? nodeData.director_name : ' '}
                onChange={() => {}}
                label=""
              />
              <InputWithLabel
                disabled={disabled}
                className={styles.containerMinor}
                value={nodeData.director_inn ? nodeData.director_inn : ' '}
                onChange={() => {}}
                label=""
              />
            </>
          )}
        </div>
        <div className={styles.container}>
          <InputWithLabel
            disabled={disabled}
            className={styles.containerLarge}
            value={nodeData.okved ? nodeData.okved : ' '}
            onChange={() => {}}
            label=" "
            isToolTip
            toolTipText={nodeData.okved ? nodeData.okved : ' '}
            toolTipID={'okved'}
          />

          <InputWithLabel
            disabled={disabled}
            className={styles.containerMinor}
            value={nodeData.okved_code ? nodeData.okved_code : ' '}
            onChange={() => {}}
            label=" "
          />
        </div>
        <div className={styles.container}>
          <InputWithLabel
            disabled={disabled}
            className={styles.containerLarge}
            value={nodeData.seller_count ? nodeData.seller_count : ' '}
            onChange={() => {}}
            label=" "
          />

          <InputWithLabel
            disabled={disabled}
            className={styles.containerMinor}
            value={nodeData.buyers_count ? nodeData.buyers_count : ' '}
            onChange={() => {}}
            label=" "
          />
        </div>

        {nodeData[type_participant]?.declaration && (
          <>
            <div className={styles.container}>
              <InputWithLabel
                disabled={disabled}
                className={styles.containerLarge}
                value={
                  nodeData[type_participant]?.declaration?.reg_number
                    ? nodeData[type_participant]?.declaration?.reg_number
                    : ' '
                }
                onChange={() => {}}
                label="  "
              />
              <InputWithLabel
                disabled={disabled}
                className={styles.containerMinor}
                value={
                  nodeData[type_participant]?.declaration?.vat_amount
                    ? formatMoneyDetailedNode(nodeData[type_participant]?.declaration?.vat_amount)
                    : ' '
                }
                onChange={() => {}}
                label={
                  '    ' +
                  (type_participant === 'ru_participant' ? '' : 'Br')
                }
              />
            </div>

            <div className={styles.container}>
              <InputWithLabel
                disabled={disabled}
                className={styles.containerLarge}
                value={
                  nodeData[type_participant]?.declaration?.submission_date
                    ? formatDate(nodeData[type_participant]?.declaration?.submission_date)
                    : ' '
                }
                onChange={() => {}}
                label="  "
              />

              <InputWithLabel
                disabled={disabled}
                className={styles.containerMinor}
                value={defaultValue ? defaultValue : ' '}
                onChange={() => {}}
                label="  "
              />
            </div>
          </>
        )}
      </div>
    );
  }
}

export default ITAISParticipant;
