import React from 'react';
import { ITransferListItemProps } from './TransferListItem.types';
import styles from './TransferListItem.module.scss';

/**
 *    
 */

const TransferListItem = ({
  transferIsEnabled,
  label,
  title,
  onClick,
  isEKP,
}: ITransferListItemProps) => {
  return (
    <div className={styles.container}>
      <span
        className={`${styles.item} ${!transferIsEnabled ? styles.disabled : ''}`}
        title={
          !isEKP
            ? '     '
            : !transferIsEnabled
            ? '   '
            : title
        }
        onClick={transferIsEnabled ? onClick : () => false}
      >
        {label}
      </span>
    </div>
  );
};

export default TransferListItem;
